const option = {
  title: {
    text: '设备在线统计',
    left: 50,
    textStyle: {
      color: '#fff'
    }
  },
  color: ['#5080f7', '#d0656b'],
  tooltip: {
    trigger: 'item',
    formatter: '{b} : {c} ({d}%)'
  },
  legend: {
    // orient: 'vertical',
    // top: 'middle',
    bottom: 30,
    left: 'center',
    data: ['在线设备', '离线设备'],
    textStyle: {
      color: '#fff'
    }
  },
  series: [
    {
      type: 'pie',
      radius: [40, 90],
      center: ['50%', '50%'],
      selectedMode: 'single',
      startAngle: 30,
      label: {
        fontSize: 14,
        fontWeight: 'bold',
        verticalAlign: 'top',
        formatter: function (params) {
          return params.name + '(' + Math.round(params.percent) + '%)'
        }
      },
      data: [
        {value: 4, name: '在线设备'},
        {value: 1, name: '离线设备'}
      ],
      itemStyle: {
        emphasis: {
          shadowBlur: 10,
          shadowOffsetX: 0,
          shadowColor: 'rgba(0, 0, 0, 0.5)'
        }
      }
    }
  ]
}

export default option



