<template>
  <div class="container-title">
    <div class="radius-left-up"></div>
    <div class="radius-right-up"></div>
    <h4>{{title}}</h4>
    <div class="radius-left-down"></div>
    <div class="radius-right-down"></div>
  </div>
</template>
<script>
  export default {
    props: ['title']
  }
</script>
<style scoped lang="less">
  .container-title {
    position: relative;
    background-color: rgba(169, 221, 252, .1);
    font-family: '宋体';
    height: 50px;
    padding: 0 30px;
    h4{
      font-size: 26px;
      line-height: 50px;
      margin: 0;
    }
    color: #cefafe;
    text-align: center;
    min-width: 175px;
    max-width: 210px;
  }

  .radius-left-up {
    position: absolute;
    display: inline-block;
    left: -6px;
    top: -6px;
    width: 14px;
    height: 14px;
    border-left: solid 2px #366a91;
    border-top: solid 2px #366a91;
  }

  .radius-right-up {
    position: absolute;
    display: inline-block;
    right: -6px;
    top: -6px;
    width: 14px;
    height: 14px;
    border-right: solid 2px #366a91;
    border-top: solid 2px #366a91;
  }

  .radius-left-down {
    position: absolute;
    display: inline-block;
    left: -6px;
    bottom: -6px;
    width: 14px;
    height: 14px;
    border-left: solid 2px #366a91;
    border-bottom: solid 2px #366a91;
  }

  .radius-right-down {
    position: absolute;
    display: inline-block;
    right: -6px;
    bottom: -6px;
    width: 14px;
    height: 14px;
    border-right: solid 2px #366a91;
    border-bottom: solid 2px #366a91;
  }
</style>



