import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/page/Main'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'Main',
      components: {
        default: Main
      }
    }
  ]
})

router.beforeEach((to, from, next) => {
  // 路由守卫，自行定义，具体参考vue-router
  next()
})

export default router



