<script>
import mapStyle from '../assets/data/map-style'
import environmentOption from '../assets/data/left-line-environment'
import energyOption from '../assets/data/left-bar-energy'
import stationOption from '../assets/data/left-bar-station'
import stationPieOption from '../assets/data/left-pie-station'
import wifiOption from '../assets/data/right-line-wifi'
import ContainerTitle from '../components/ContainerTitle'
import Container from '../components/Container'
import VSelect from '@alfsnd/vue-bootstrap-select'
import environmentJsonData from '../assets/data/environmentJsonData'
import energyData from '../assets/data/energyData'
import HkVideo from '../components/HK'
// import dataHttp from '../model/dataHttp'
export default {
  name: 'Main',
  data () {
    return {
      gisIcon: require('../assets/imgs/project-icon.png'), // 地图图标
      advertData: [ // 广告列表
        {img: 'advert1.jpg', type: '图片'},
        {img: 'advert2.png', type: '图片'},
        {img: 'advert3.jpg', type: '图片'},
        {img: 'advert4.jpg', type: '图片'},
        {img: 'advert5.jpg', type: '图片'},
        {img: 'advert6.jpg', type: '图片'},
        {img: 'advert7.jpg', type: '图片'}
      ],
      environmentSelected: 'temperature',
      environmentJsonData: environmentJsonData,
      energyData: energyData,
      dateSelected: 'day',
      projectSelectedValue: null, // 当前所选项目
      projectSelectedCodeValue: null, // 当前所选项目编号
      projectList: [ // 项目列表
        {value: 1, text: '北京项目'},
        {value: 2, text: '海南项目'}
      ],
      allCodeList: [ // 所有编号列表
        {value: '01001', text: '29950606', parent: 1},
        {value: '01002', text: '29950608', parent: 1},
        {value: '01003', text: '29950611', parent: 1},
        {value: '01004', text: '29950618', parent: 2},
        {value: '01005', text: '29950628', parent: 2}
      ],
      projectCodeList: [ // 当前项目下编号列表
      ],
      mapStyle: mapStyle, // 地图风格
      bMap: null, // 地图对象
      environmentOption: environmentOption, // 页面图表配置
      energyOption: energyOption,
      stationOption: stationOption,
      stationPieOption: stationPieOption,
      wifiOption: wifiOption,
      advertSelectIndex: 0, // 当前广告索引
      advertTime: null,
      nextVideo: require('../assets/hkJs/video4.mp4')
    }
  },
  components: {
    'n-container-title': ContainerTitle,
    'n-container': Container,
    VSelect,
    HkVideo
  },
  watch: {
    projectSelectedValue: function (val, oldVal) {
      let codeArr = []
      this.allCodeList.forEach(d => {
        if (d.parent === val.value) {
          codeArr.push(d)
        }
      })
      this.projectCodeList = codeArr
      this.projectSelectedCodeValue = this.projectCodeList[0]
    },
    environmentSelected: function (val) {
      this.changeEnvironment(val)
    },
    dateSelected: function (val) {
      this.changeEnergy(val)
      this.changeEnergyTime()
    }
  },
  methods: {
    random (lower, upper) { // 生成任意值之间的整数
      return Math.floor(Math.random() * (upper - lower)) + lower
    },
    randomFixed (lower, upper) { // 生成小数
      return Math.random() * (upper - lower) + lower
    },
    changeAdvert (a, i) {
      if (this.advertSelectIndex === i) return
      this.advertSelectIndex = i
      this.$refs.advertImgOrVideo.src = require('../assets/imgs/' + this.advertData[i].img)
    },
    handler ({BMap, map}) {
      map.enableScrollWheelZoom(true)
      this.bMap = BMap
    },
    changeStation () { // 设备在线情况改变
      let options = this.$refs.stationPie.options
      if (options.series[0].data[0].value === 4) {
        options.series[0].data[0].value = 3
        options.series[0].data[1].value = 2
      } else {
        options.series[0].data[0].value = 4
        options.series[0].data[1].value = 1
      }
      this.$refs.stationPie.mergeOptions(options)
    },
    changeEnergy (val) {
      this.energyOption.xAxis[0].data = this.energyData[val].axis
      this.energyOption.series[0].data = this.energyData[val].number
      // let options = {
      //   xAxis: [{
      //     data: this.energyData[val].axis
      //   }],
      //   series: [
      //     {
      //       data: this.energyData[val].number
      //     }
      //   ]
      // }
      // this.$refs.energy.mergeOptions(options)
    },
    changeEnergyTime () {
      let options = this.$refs.energy.options
      switch (this.dateSelected) {
        case 'day':
          let hours = new Date().getHours()
          options.series[0].data.forEach((d, i) => {
            if (i > hours) {
              options.series[0].data[i] = 0
            } else if (i === hours) {
              options.series[0].data[i] = options.series[0].data[i] === 0 ? Math.floor(this.randomFixed(0.02, 0.7) * 100) / 100 : Number((options.series[0].data[i] + Math.floor(this.randomFixed(0.02, 0.07) * 100) / 100).toFixed(2))
            } else {
              options.series[0].data[i] = options.series[0].data[i] === 0 ? this.random(1, 7) : options.series[0].data[i]
            }
          })
          break
        case 'month':
          options.series[0].data[4] += Math.floor(this.randomFixed(0.05, 0.1) * 100) / 100
          break
        case 'year':
          options.series[0].data[4] += Math.floor(this.randomFixed(0.05, 0.1) * 100) / 100
          break
      }
      this.$refs.energy.mergeOptions(options)
    },
    changeWifi () {
      let hours = new Date().getHours()
      let options = this.$refs.wifi.options
      if (hours >= 11 && hours <= 13) {
        let increment = this.random(10, 50)
        options.series[0].data[4] = options.series[0].data[4] === 0 ? 213 : options.series[0].data[4] + increment
        options.series[1].data[4] += increment
        options.series[2].data[4] += increment
      } else if (hours >= 14 && hours <= 17) {
        let increment = this.random(10, 30)
        options.series[0].data[4] = 4520
        options.series[0].data[5] = options.series[0].data[5] === 0 ? 122 : options.series[0].data[5] + increment
        options.series[1].data[5] += increment
        options.series[2].data[5] += increment
      } else if (hours >= 9 && hours <= 12) {
        let increment = this.random(10, 50)
        options.series[0].data[3] = options.series[0].data[3] === 0 ? 382 : options.series[0].data[3] + increment
        options.series[1].data[3] += increment
        options.series[2].data[3] += increment
      }
      this.$refs.wifi.mergeOptions(options)
    }, // 流量
    changeEnvironment (val) {
      let options = {
        yAxis: {
          name: this.environmentJsonData[val].name,
          axisLabel: {
            formatter: '{value} ' + this.environmentJsonData[val].unit
          }
        },
        series: [
          {
            data: this.environmentJsonData[val].today
          },
          {
            data: this.environmentJsonData[val].yesterday
          }
        ]
      }
      this.$refs.environment.mergeOptions(options)
    }, // 环境监测
    changeEnvironmentTime () {
      let val = this.environmentSelected
      this.environmentJsonData[val].today.forEach((d, i) => {
        if (i >= new Date().getHours()) {
          this.environmentJsonData[val].today[i] = (this.environmentJsonData[val].today[i] * this.randomFixed(0.98, 1.02)).toFixed(0)
          if (val === 'temperature') {
            this.environmentJsonData[val].today[i] = this.environmentJsonData[val].today[i] > 30 ? 30 : this.environmentJsonData[val].today[i]
            this.environmentJsonData[val].today[i] = this.environmentJsonData[val].today[i] < 14 ? 14 : this.environmentJsonData[val].today[i]
          }
        }
      })
      let options = {
        series: [
          {
            data: this.environmentJsonData[val].today
          },
          {
            data: this.environmentJsonData[val].yesterday
          }
        ]
      }
      this.$refs.environment.mergeOptions(options)
    }, // 环境监测定时
    initEnvironmentJsonData () {
      let todayPressure = []
      let todaySpeed = []
      let todayPrecipitation = []
      let yesterdaySpeed = []
      let yesterdayPressure = []
      let yesterdayPrecipitation = []
      for (var i = 0; i < 24; i++) {
        todayPressure.push(this.random(700, 1300))
        todaySpeed.push(this.random(0, 3))
        todayPrecipitation.push(this.random(0, 0))
        yesterdaySpeed.push(this.random(0, 4))
        yesterdayPressure.push(this.random(600, 1200))
        yesterdayPrecipitation.push(this.random(0, 0))
      }
      this.environmentJsonData['pressure'].today = todayPressure
      this.environmentJsonData['speed'].today = todaySpeed
      this.environmentJsonData['precipitation'].today = todayPrecipitation
      this.environmentJsonData['pressure'].yesterday = yesterdayPressure
      this.environmentJsonData['speed'].yesterday = yesterdaySpeed
      this.environmentJsonData['precipitation'].yesterday = yesterdayPrecipitation
    }, // 初始化环境数据
    stopAdvert () {
      clearInterval(this.advertTime)
    },
    startAdvert () {
      this.advertTime = setInterval(() => {
        this.advertSelectIndex++
        if (this.advertSelectIndex === this.advertData.length) {
          this.advertSelectIndex = 0
        }
        this.$refs.advertImgOrVideo.src = require('../assets/imgs/' + this.advertData[this.advertSelectIndex].img)
      }, 30000)
    }
  },
  mounted () {
    this.changeStation()
    this.changeWifi()
    this.changeEnergyTime()
    setInterval(() => {
      this.changeEnvironmentTime()
      this.changeStation()
      this.changeWifi()
      this.changeEnergyTime()
    }, 180000)
    this.startAdvert()
    // setTimeout(() => {
    //   this.$refs.video2.src = this.nextVideo
    // }, 5000)
    this.changeEnvironment(this.environmentSelected)
    this.initEnvironmentJsonData()
    this.projectSelectedValue = this.projectList[0]
    this.allCodeList.forEach(d => {
      if (d.parent === this.projectSelectedValue.value) {
        this.projectCodeList.push(d)
      }
    })
  }
}
</script>
