                      }
                    }

                    tbody {
                      width: 100%;
                      overflow-y: scroll;
                      display: block;
                      height: calc(334px - 34px);

                      tr {
                        height: 33px;
                        vertical-align: center;
                        display: table;
                        width: 100%;
                        table-layout: fixed;
                      }

                      .active {
                        background: #3a5f82;
                      }
                    }
                  }

                  .advert-btn {
                    ul {
                      padding-top: 20px;

                      li {
                        width: 100%;
                        text-align: center;
                        list-style: none;
                        margin-bottom: 20px;
                        cursor: pointer;
                      }
                    }
                  }
                }
              }

              > div:nth-of-type(2) {
                flex: 0.9;
              }
            }
          }

          .right-right {
            flex: 1;
            height: 100%;

            .title-content {
              flex-wrap: wrap;

              > div {
                width: 100%;
              }

              > div:nth-of-type(1) {
                margin-right: 220px;
                margin-bottom: 20px;
                padding-left: 0;
              }

              > div:nth-of-type(2) {
                margin-right: 20px;
                padding-left: 0;
              }
            }

            .video-time {
              display: flex;
              flex-wrap: wrap;
              padding-left: 0;
              justify-content: space-between;
              width: 100%;
              padding-right: 50px;
              margin-bottom: 20px;

              > div {
                video {
                  width: 100%;
                }

                width: 100%;
                height: auto;
                border: 1px solid #38aee1;
              }
            }

          }
        }
      }

      margin: 0 30px 0;
      padding-bottom: 20px;
    }
  }

  .left-bottom {
    .title-content {
      > div {
        flex: 1.2;
        margin-right: 20px;
      }

      > div:nth-of-type(1) {
        margin-right: 50px;
        flex: 1;
      }

      > div:nth-of-type(2) {
        padding-left: 50px;
      }

      > div:nth-of-type(3) {
        flex: 3.83;
      }

    }
  }


  .n-select-tab {
    position: absolute;
    right: 20px;
    top: 15px;
    z-index: 100;
  }

  /* 中部样式 start */

  .baidu-map {
    width: 100%;
    overflow-y: hidden;
    height: 100% !important;
  }

</style>



