// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import VueAxios from 'vue-axios'
import BaiduMap from 'vue-baidu-map'
import App from './App'
import store from './store'
import router from './router'
import BootstrapVue from 'bootstrap-vue'
// import 'babel-polyfill'

import './assets/fonts/iconfont.css'
import './assets/css/base.css'
import ECharts from 'vue-echarts/components/ECharts.vue'
// 按需导入图表，具体可参考github
import 'echarts/lib/chart/bar'
import 'echarts/lib/chart/line'
import 'echarts/lib/chart/pie'
import 'echarts/lib/chart/radar'
import 'echarts/lib/chart/scatter'
import 'echarts/lib/chart/map'
import 'echarts/lib/chart/effectScatter'
import 'echarts/lib/component/tooltip'
import 'echarts/lib/component/title'
import 'echarts/lib/component/legend'
import 'echarts/lib/component/grid'
import 'echarts/lib/component/markPoint'
import 'echarts/lib/component/markLine'
import 'echarts/lib/component/visualMap'
import 'echarts/lib/component/geo'
import config from './config/config_dev' // 开发环境使用
// import VueWebsocket from 'vue-websocket'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
// import config from './config/config' // 生产环境使用
Vue.component('chart', ECharts)
Vue.use(BootstrapVue)
Vue.config.productionTip = false
Vue.use(Vuex)
// Vue.use(VueWebsocket, 'ws://58.250.161.102:9772', {reconnection: true})
Vue.use(BaiduMap, {
  // ak 是在百度地图开发者平台申请的密钥 详见 http://lbsyun.baidu.com/apiconsole/key */
  ak: 'kG0l1mn4xUHoTGvG1cxXfm613Cvw0twf'
})
Vue.use(VueAxios, axios)
// 挂载全局属性、用于发布订阅事件
Vue.hub = Vue.prototype.$hub = new Vue()
Vue.http = Vue.prototype.$http
// 挂载全局配置
Vue.conf = Vue.prototype.$config = config
// 挂载工具函数（自己后期定义）
// 挂载地图对象到实例
Vue.prototype.$echarts = ECharts
/* eslint-disable no-new */
window.vue = new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: {App}
})



